@use "base/variables" as v;

.SolutionSection{
  padding: 0;
  padding-top: 5rem;
  background: v.$blanco;
  border-radius: 2rem 2rem 0 0;
  margin-top: -2rem;
  position: relative;
  overflow: hidden;
  margin-bottom: 4rem;
  &-title{
    text-transform: uppercase;
    color: v.$grisOscuro;
    text-align: center;
    margin-bottom: 2rem;
  }
  &-gallery{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 12px;
    width: 100%;
    /*max-width: 120rem;
    margin: 0 auto;*/
    padding: 1rem 10px;
    &__item{
      position: relative;
      transform: rotate(0deg) translateX(0); 
      transition: transform 0.5s ease-out;
      border: 2px solid v.$amarilloOpaco;
      border-radius: 3rem;
      box-sizing: border-box;
      padding: .7rem;
      &-img{
        object-fit: cover;
        width: 100%;
        height: 400px;
        border-radius: 3rem;
        @media (max-width: 800px){
          height: 350px;
          object-position: top;
        }

        &:hover {
        transform: scale(1.05);
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
      }

      @media (max-width: 800px) {
        height: 350px;
        object-position: top;
        cursor: pointer;
      }
      }
      &:nth-child(1){
        transform: rotate(10deg) translateX(-3%) translateY(8%);
        z-index: 1;
        @media (max-width: 800px){
          transform: none;
        }
      }
      &:nth-child(2){
        transform: rotate(-11deg) translateX(-8%) translateY(11%);
        z-index: 2;
        @media (max-width: 800px){
          transform: none;
        }
      }
      &:nth-child(3){
        transform: rotate(15deg) translateX(0%) translateY(15%);
        z-index: 3;
        @media (max-width: 800px){
          transform: none;
        }
      }
    }
    @media (max-width: 800px){
  grid-template-columns: repeat(3, 1fr);
  overflow-x: auto;
}
  }

  &-content{
    max-width: 95rem;
    width: 90%;
    margin: 0 auto;
    margin-top: 3rem;
    text-align: center;
    &__title{
      font-weight: 700;
      font-size: v.$largeText-1;
      color: v.$grisOscuro;
      line-height: 1;
      margin: 0;
      margin-top: 10rem;
      @media (max-width: 800px){
      margin-top: 3rem;
    }
    }
    &__detail{
      font-size: 3.5rem;
      border-bottom: 8px solid v.$amarilloOpaco;
      width: fit-content;
      margin: 0 auto;
      margin-bottom: 3rem;
    margin-top: 3rem;
    line-height: inherit;

    
    }
    &__description{
      font-size: v.$font-size-h1;
      margin: 0;
      line-height: 1.2;
      z-index: 1;
      position: relative;
    }
  }

  & .shape{
    position: absolute;
    bottom: -6%;
    left: -19%;
    width: 29%;
    @media (max-width: 800px){
      display: none;
    }
  }
}