html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #000000;
  font-size: 2rem;
}
body.overflow-hidden {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 4.6rem;
}

h3 {
  font-size: 6rem;
  text-align: center;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

body > section {
  padding: 5rem 0;
}

/* Etiquetas auxiliares Heading */
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-weight: 700;
  line-height: 1.2;
  text-rendering: optimizeLegibility;
  margin: 0;
}

.title-large-1, .title-large-2 {
  font-weight: 900;
  font-size: 5.5rem;
  line-height: 1;
}

.title-large-2 {
  font-size: 5rem;
}

.fbold {
  font-weight: bold;
}

.Header-contenedor {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.Header-figure {
  margin: 0;
  margin-top: 2rem;
}
.Header-figure__logo {
  max-width: 190px;
  width: 70%;
}
.Header-form {
  position: relative;
}
.Header-form__search {
  outline: none;
  border-radius: 20px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  border-left: 1px solid #000000;
  font-size: 17px;
  padding: 0.4rem 0;
  padding-left: 1rem;
}
.Header-form__icon {
  position: absolute;
  right: 0;
  height: 100%;
  padding: 0 0.8rem;
  border-radius: 0 20px 20px 0;
  display: flex;
  align-items: center;
  top: 0;
  border-left: 1px solid #000;
  font-size: 17px;
}
@media (max-width: 1080px) {
  .Header-form {
    display: none;
  }
}
.Header-nav-unorden {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
}
.Header-nav-unorden__list {
  list-style: none;
}
.Header-nav-unorden__list-link {
  font-size: 16px;
  color: #000000;
}
@media (max-width: 895px) {
  .Header-nav {
    display: none;
  }
}
.Header-btnLink {
  background-color: #2a4321;
  color: #FFFFFF;
  border-radius: 30px;
  padding: 1.5rem 4rem;
  font-size: 16px;
  font-weight: 600;
}
@media (max-width: 1080px) {
  .Header-btnSearch {
    display: block;
  }
}
@media (max-width: 895px) {
  .Header-btnSearch {
    display: none;
  }
}
.Header-btnSearch__btn {
  background-color: transparent;
  border: 0;
  padding: 0;
  margin-right: 1rem;
}
.Header-btnSearch__btn i {
  font-size: 2.2rem;
}
@media (min-width: 1080px) {
  .Header-btnSearch__btn {
    display: none;
  }
}
.Header__hamburguer-menu {
  display: none;
  background-color: transparent;
  border: none;
}
.Header__hamburguer-menu i {
  font-size: 2.8rem;
  color: #2a4321;
}
@media (max-width: 895px) {
  .Header__hamburguer-menu {
    display: block;
  }
}
.Header__menu-offcanvas {
  position: fixed;
  top: 0;
  bottom: 0;
  background-color: rgba(33, 33, 33, 0.4117647059);
  right: 0;
  left: 0;
  height: 100%;
  z-index: 100;
  transition: 0.3s all linear;
  transform: translateX(-100%);
}
.Header__menu-offcanvas-content {
  width: 60vw;
  height: 100vh;
  background-color: #2a4321;
  padding: 2rem;
}
.Header__menu-offcanvas-content__close {
  margin-bottom: 2rem;
  text-align: right;
}
.Header__menu-offcanvas-content__close-btn {
  background: transparent;
  border: 0;
  padding: 0;
}
.Header__menu-offcanvas-content__close-btn i {
  font-size: 3.5rem;
  color: white;
}
.Header__menu-offcanvas__navigation {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.Header__menu-offcanvas__navigation-link {
  text-decoration: none;
  color: #FFFFFF;
}
.Header__menu-offcanvas-form {
  position: relative;
  margin-bottom: 3rem;
}
.Header__menu-offcanvas-form__search {
  outline: none;
  border-radius: 20px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  border-left: 1px solid #000000;
  font-size: 17px;
  padding: 0.4rem 0;
  padding-left: 1rem;
  width: 100%;
}
.Header__menu-offcanvas-form__icon {
  position: absolute;
  right: 0;
  height: 100%;
  padding: 0 0.8rem;
  border-radius: 0 20px 20px 0;
  display: flex;
  align-items: center;
  top: 0;
  border-left: 1px solid #000;
  font-size: 17px;
}
.Header__menu-offcanvas-btnLink {
  background-color: #ffc900;
  color: #FFFFFF;
  border-radius: 30px;
  padding: 1.5rem 4rem;
  font-size: 16px;
  font-weight: 600;
  display: block;
  margin: 0 auto;
  margin-top: 2rem;
  text-align: center;
  width: fit-content;
}

.menuOffCanvas-hidden {
  transform: translateX(0px);
}

.Banner {
  background-image: url("/build/img/wp-build.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  background-attachment: fixed;
  /*height: 120vh;*/
  margin-top: 3rem;
  position: relative;
  padding: 0;
  padding-bottom: 25rem;
  /*@media (max-width: 1170px){
    height: 80vh;
  }*/
}
@media (max-width: 800px) {
  .Banner {
    background-position: bottom;
    background-attachment: local;
  }
}
.Banner-contenedor {
  display: flex;
  justify-content: flex-end;
}
.Banner-overlay {
  background-image: linear-gradient(0deg, #000 0, transparent 47%, #fff 87%);
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
@media (max-width: 800px) {
  .Banner-overlay {
    background-image: linear-gradient(0deg, black 0%, transparent 61%, white 113%);
  }
}
.Banner-content {
  position: relative;
  z-index: 10;
  max-width: 80rem;
  border-right: 10px solid #ffc900;
  padding-right: 15px;
}
.Banner-content__title {
  display: flex;
  flex-direction: column;
  text-align: right;
}
.Banner-content__title-s1 {
  font-weight: 400;
}
.Banner-content__title-s2 {
  color: #2a4321;
}
.Banner-content-text {
  font-size: 16px;
  text-align: right;
  line-height: 1.2;
  margin-bottom: 0;
  font-weight: 500;
}

@media (max-width: 768px) {
  .Banner-contenedor {
    background: rgba(255, 255, 255, 0.3882352941);
    border-radius: 10px;
    padding: 0px 10px;
  }
}

@media (max-width: 768px) {
  .Banner {
    background-image: url("/build/img/wp-build-movil.jpeg");
    padding-top: 2rem;
  }
}

.SolutionSection {
  padding: 0;
  padding-top: 5rem;
  background: #FFFFFF;
  border-radius: 2rem 2rem 0 0;
  margin-top: -2rem;
  position: relative;
  overflow: hidden;
  margin-bottom: 4rem;
}
.SolutionSection-title {
  text-transform: uppercase;
  color: #333333;
  text-align: center;
  margin-bottom: 2rem;
}
.SolutionSection-gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  width: 100%;
  /*max-width: 120rem;
  margin: 0 auto;*/
  padding: 1rem 10px;
}
.SolutionSection-gallery__item {
  position: relative;
  transform: rotate(0deg) translateX(0);
  transition: transform 0.5s ease-out;
  border: 2px solid #ffc900;
  border-radius: 3rem;
  box-sizing: border-box;
  padding: 0.7rem;
}
.SolutionSection-gallery__item-img {
  object-fit: cover;
  width: 100%;
  height: 400px;
  border-radius: 3rem;
}
@media (max-width: 800px) {
  .SolutionSection-gallery__item-img {
    height: 350px;
    object-position: top;
  }
}
.SolutionSection-gallery__item-img:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}
@media (max-width: 800px) {
  .SolutionSection-gallery__item-img {
    height: 350px;
    object-position: top;
    cursor: pointer;
  }
}
.SolutionSection-gallery__item:nth-child(1) {
  transform: rotate(10deg) translateX(-3%) translateY(8%);
  z-index: 1;
}
@media (max-width: 800px) {
  .SolutionSection-gallery__item:nth-child(1) {
    transform: none;
  }
}
.SolutionSection-gallery__item:nth-child(2) {
  transform: rotate(-11deg) translateX(-8%) translateY(11%);
  z-index: 2;
}
@media (max-width: 800px) {
  .SolutionSection-gallery__item:nth-child(2) {
    transform: none;
  }
}
.SolutionSection-gallery__item:nth-child(3) {
  transform: rotate(15deg) translateX(0%) translateY(15%);
  z-index: 3;
}
@media (max-width: 800px) {
  .SolutionSection-gallery__item:nth-child(3) {
    transform: none;
  }
}
@media (max-width: 800px) {
  .SolutionSection-gallery {
    grid-template-columns: repeat(3, 1fr);
    overflow-x: auto;
  }
}
.SolutionSection-content {
  max-width: 95rem;
  width: 90%;
  margin: 0 auto;
  margin-top: 3rem;
  text-align: center;
}
.SolutionSection-content__title {
  font-weight: 700;
  font-size: 5.5rem;
  color: #333333;
  line-height: 1;
  margin: 0;
  margin-top: 10rem;
}
@media (max-width: 800px) {
  .SolutionSection-content__title {
    margin-top: 3rem;
  }
}
.SolutionSection-content__detail {
  font-size: 3.5rem;
  border-bottom: 8px solid #ffc900;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 3rem;
  margin-top: 3rem;
  line-height: inherit;
}
.SolutionSection-content__description {
  font-size: 2.5rem;
  margin: 0;
  line-height: 1.2;
  z-index: 1;
  position: relative;
}
.SolutionSection .shape {
  position: absolute;
  bottom: -6%;
  left: -19%;
  width: 29%;
}
@media (max-width: 800px) {
  .SolutionSection .shape {
    display: none;
  }
}

.ServiceList {
  background-color: #ffc900;
  border-radius: 3rem 3rem 0 0;
  overflow: hidden;
  position: relative;
}
.ServiceList-contenedor {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
}
@media (max-width: 1000px) {
  .ServiceList-contenedor {
    grid-template-columns: none;
  }
}
.ServiceList-left__title {
  text-transform: uppercase;
  color: #333333;
  margin: 0;
}
@media (max-width: 1168px) {
  .ServiceList-left__title {
    font-size: 6rem;
  }
}
.ServiceList-left__subtitle {
  font-weight: 700;
  font-size: 5.5rem;
  color: #333333;
  line-height: 1;
  margin: 0;
  margin-bottom: 4rem;
}
@media (max-width: 1168px) {
  .ServiceList-left__subtitle {
    font-size: 5rem;
  }
}
.ServiceList-left__unorden {
  margin: 0;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  row-gap: 17px;
}
.ServiceList-left__unorden-list {
  list-style: none;
}
.ServiceList-left__unorden-list__text {
  color: #333333;
  font-size: 3.5rem;
  line-height: 1;
  margin: 0;
}
@media (max-width: 1168px) {
  .ServiceList-left__unorden-list__text {
    font-size: 3.2rem;
  }
}
.ServiceList-right {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 2rem;
}
.ServiceList-right__image-wrapper {
  position: relative;
  width: 80%;
  transform: rotate(0deg);
  transform-origin: center;
  padding-bottom: 70%;
}
.ServiceList-right__image-wrapper img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 24px;
  display: block;
}
.ServiceList-right__image-wrapper:after {
  content: "";
  border: 2px solid #333333;
  display: block;
  position: absolute;
  top: -20px;
  left: -20px;
  width: 100%;
  height: 100%;
  border-radius: 24px;
  z-index: -1;
}

.prueba {
  position: absolute;
  right: 0;
  top: 0;
  object-fit: contain;
  width: 90%;
  max-width: 650px;
  height: 100%;
  display: flex;
  align-items: center;
}
.prueba-line {
  border: 2px solid #333;
  border-radius: 25px;
  transform: rotate(10deg);
}
.prueba-line img {
  transform: rotate(-10deg);
}
@media (max-width: 1240px) {
  .prueba {
    right: -100px;
  }
}
@media (max-width: 1168px) {
  .prueba {
    right: -158px;
  }
}
@media (max-width: 1000px) {
  .prueba {
    position: static;
    margin: 0 auto;
    margin-top: 5rem;
  }
}

.ServiceList-left {
  padding-left: 20px;
}

.Slides {
  background-color: #333333;
  padding: 3rem 2rem;
}
.Slides-title {
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  margin-bottom: 1rem;
}
.Slides-subtitle {
  font-size: 3.5rem;
  border-bottom: 8px solid #ffc900;
  width: fit-content;
  text-transform: uppercase;
  margin: 0 auto;
  margin-bottom: 3rem;
  color: #FFFFFF;
  line-height: inherit;
  text-align: center;
}
.Slides-content {
  max-width: 95rem;
  width: 90%;
  margin: 0 auto;
  text-align: center;
}
.Slides-content__description {
  font-size: 2.5rem;
  margin: 0;
  line-height: 1.2;
  color: #FFFFFF;
}

.swiper {
  width: 100%;
  padding: 50px 0;
}

.swiper-slide {
  position: relative;
  width: 320px;
  aspect-ratio: 3/4;
  border-radius: 14px;
  border: 1px solid rgba(177, 177, 177, 0.4);
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: inherit;
  user-select: none;
}

.title {
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate -50%, -20%;
  -ms-transform: translate(-50%, -20%);
  width: max-content;
  text-align: center;
  padding: 10px 18px;
  background: rgba(46, 39, 39, 0.3);
  border-radius: 8px;
  border: 2px solid rgba(177, 177, 177, 0.4);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur 10px;
  transition: all 0.5s linear;
}

.title span {
  font-size: 1.2rem;
  font-weight: 600;
}

.CallToActionSection {
  border-radius: 2rem;
  margin-top: -2rem;
  background-color: white;
  padding: 4rem 0;
}
.CallToActionSection-title {
  text-transform: uppercase;
  color: #333333;
  background-color: #ffc900;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 1rem;
  padding: 2rem 2rem;
  width: fit-content;
  font-size: 4rem;
}
.CallToActionSection-subtitle {
  text-transform: uppercase;
